body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  scrollbar-width: none;  
  -ms-overflow-style: none;

  &::-webkit-scrollbar{
    width: 0;
  }
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

input[type=range]::-webkit-slider-thumb {
  background-color: #283593;
}
input[type=range]::-moz-range-thumb {
  background-color: #283593;
}
input[type=range]::-ms-thumb {
  background-color: #283593;
}

*{
  box-sizing: border-box;
}

.brand-logo{
  margin-left: 20px;
}

.App{
  height: 100vh;
  min-height: 820px;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
 
.Layout{
  flex-grow: 1;
  display: flex;
  height: calc(100% - 214px);
}

.Ranges{
  border-right: 1px solid rgb(194, 194, 194);
  padding: 20px;
  width: 30%;
}

.Range{
  margin-bottom: 25px;
  label{
    font-size: 21px;
    text-transform: uppercase;
  }
}

.MainImage{
  width: 70%;
  padding: 20px;
}

.img-wrap{
  width: 100%;
  height: 100%;
  img{
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.Filters{
  height: 150px;
  background: #283593;
  display: flex;
  align-items: center;
  flex-wrap: nowrap;
  overflow-y: auto;
  padding: 0 20px;
  scrollbar-width: none;  
  -ms-overflow-style: none;

  &::-webkit-scrollbar{
    width: 0;
  }

  h3{
    color: white;
    text-align: center;
    margin: 0 auto;
  }

  .Filter{
    width: 170px;
    height: 110px;
    overflow: hidden;
    margin-right: 20px;
    cursor: pointer;
    position: relative;
    flex-shrink: 0;

    .close{
      width: 30px;
      height: 30px;
      position: absolute;
      top: 0px;
      right: 0px;
      z-index: 10;
      background: white;
      display: flex;
      align-items: center;
      justify-content: center;
      img{
        width: 12px;
        height: auto;
      }
    }
  }
}

@media screen and (max-width: 1440px) {
 
  .App{
    min-height: 730px;
  }
  .Filters{
    height: 140px;
  
    .Filter{
      width: 200px;
      height: 100px;
    }
  }
  .Range{
    margin-bottom: 15px;
  }
}

@media screen and (max-width: 768px) {
  .App{
    height: auto;
    min-height: initial;
  }
  .Filters{
    height: 140px;
  
    .Filter{
      width: 160px;
      height: 100px;
    }
  }
  .Layout{
    flex-direction: column;
    > div{
      width: 100%;
    }
  }
}